.registration-form {
    max-width: 400px;
    height: 94vh;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    overflow-y: scroll;
  }
  
  .registration-form h1 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .table caption{
    font-size: large;
    font-weight: bold;
  }
  .table td{
    text-align: center;
    vertical-align: middle !important;
  }
  .form-group {
    margin-bottom: 15px;
  }
  .radio_npl_wrap{
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 13px;
    gap: 13px;
  }
  
  .form-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    margin-left: 10px;
    padding-top: 0.5em;
  }
  
  .form-group input {
    width: 90%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  .form-group .radio{
    display: flex;
    width: 100%;
  }
  button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  /* Media query for smaller screens */
  @media (max-width: 768px) {
    .registration-form {
      max-width: 100%;
      height: 50%;
    }

    .form-group label {
     margin-left: 0;
    }

    .logo{
      margin-right: 14%;
    }

    img{
      width: 100%;
      margin-left: 0;
    }
   
    .radio_npl_wrap {
      grid-template-columns: auto; /* Stack in a single column */
      grid-gap: 10px;
      gap: 10px; /* Smaller gap */
     margin-left: 20px;
    }
    
   
  }

  
